import * as actionTypes from '../actions/actionTypes';
import updatedObject from '../Utility';


const initialState = {
    token: null,
    error: null,
    userId: null,
    loading: false,
    authRedirectPath: '/'
}

const authStart = (state, action) => {
    return updatedObject(state, {error: null, loading: true})
}

const authFail = (state, action) => {
    return updatedObject(state, {error: action.error, loading: false})
}

const authSuccess = (state, action) => {

    return updatedObject(state, {
        token: action.token,
        userId: action.userId,
        loading: false
    })
}

const authLogout = (state, action) => {
    return updatedObject(state, {token: null, userId:null, loading: false })
}

const authRedirectPath = (state, action) => {
    return updatedObject(state, {authRedirectPath: action.path})
}
 
const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.AUTH_START: return authStart(state, action);
        case actionTypes.AUTH_SUCCESS: return authSuccess(state, action);
        case actionTypes.AUTH_FAIL: return authFail(state, action); 
        case actionTypes.AUTH_LOGOUT: return authLogout(state, action);
        case actionTypes.AUTH_REDIRECT_PATH: return authRedirectPath(state, action)   
        default:
            return state;
    }
}


export default reducer;


