import axios from 'axios';
import * as actionTypes from './actionTypes';

export const authStart = () => {
   return{
       type: actionTypes.AUTH_START
   }
}

export const authSuccess = (token, userId) => {
    return{
        type: actionTypes.AUTH_SUCCESS,
        token: token,
        userId: userId
    }
}

export const authFail = (err) => {
    return{
        type: actionTypes.AUTH_FAIL,
        error: err
    }
}

export const logout =  () => {
    localStorage.removeItem('token');
    localStorage.removeItem('expirationTime');
    localStorage.removeItem('userId');
    return {
        type: actionTypes.AUTH_LOGOUT
    }
}

export const authRedirectPath = (path) => {
    return {
        type: actionTypes.AUTH_REDIRECT_PATH,
        path: path
    }
}

const checkAuthTimeout = (expiresTime) => {
    return dispatch => {
        setTimeout( () => {
        dispatch(logout());
    }, expiresTime * 1000);
}
}

export const auth = (email, password, isSignup) => {
    return dispatch => {
        dispatch(authStart());
        const authData = {
            email: email,
            password: password,
            returnSecureToken: true
        }
        let url = "https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyDwnMG5GVQc_foX61-prPy0e5znZq3_S84";
        if(!isSignup){
            url = "https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyDwnMG5GVQc_foX61-prPy0e5znZq3_S84";
        }
        axios.post(url, authData)
        .then(res => {
            // console.log(res);
            localStorage.setItem('token', res.data.idToken);
            localStorage.setItem('expirationTime', res.data.expiresIn);
            localStorage.setItem('userId', res.data.localId);
            dispatch(authSuccess(res.data.idToken, res.data.localId));
            dispatch(checkAuthTimeout(res.data.expiresIn));
        })
        .catch(err => {
            dispatch(authFail(err.response.data.error.message))
        })
    }
}

export const authCheckState = () => {
    return dispatch => {
        const token = localStorage.getItem("token");
        if(!token){
            dispatch(logout())
        }else{
            const expirationTime = new Date(localStorage.getItem('expirationTime'));
            const userId = localStorage.getItem("userId");
          
            const date = new Date();
            if(new Date() < expirationTime){
            dispatch(authSuccess(token, userId))
            dispatch(checkAuthTimeout((expirationTime.getTime() - date.getTime()) / 1000 ))   
            }
        }
    }
}




